import { ErrorMessage } from "@hookform/error-message";
import React from "react";
import { useForm } from "react-hook-form";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { useDispatch } from "react-redux";
import { loginAction } from "../../redux/action/userAction";
import FormErrors from "../../common/components/FormErrors";
const Login = ({ className, history }) => {
  const { register, handleSubmit, errors } = useForm();
  const dispatch = useDispatch();
  // const URL = "http://127.0.0.1:5000/public/api/login";
  const handle = (data) => {
    dispatch(
      loginAction(data, (role) => {
        console.log(role === "1");
        if (role === "1") history.push("/agency-dashboard");
        if (role === "2") history.push("/");
      })
    );
  };
  return (
    <main className={`${className} form d-flex`}>
      <section className="form__img d-none d-lg-flex">
        <div className="form__img__logo">
          <img src="/images/logo-footer.png" alt="Era Logo" />
        </div>
        <p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Laudantium, libero?</p>
        <nav className="form__img__link">
          <Link to="/">Trang chủ</Link>
          <a href="">Về chúng tôi</a>
          <a href="">Bài viết</a>
          <a href="">FAQs</a>
        </nav>
      </section>
      <section className="form__form">
        <p className="fs__title--main fw-bold">Đăng nhập Ngay</p>
        <p className="fs__text">Lorem ipsum dolor sit amet consectetur adipisicing elit.</p>
        <form onSubmit={handleSubmit(handle)}>
          <input
            type="text"
            name="name"
            placeholder="Username"
            ref={register({ required: "Bạn cần nhập đầy đủ thông tin" })}
          />
          <input
            type="password"
            name="password"
            placeholder="Mật khẩu"
            ref={register({ required: "Bạn cần nhập đầy đủ thông tin" })}
          />
          <FormErrors errors={errors} />

          <div className="text-end my-1">
            <a href="#">Quên mật khẩu</a>
          </div>
          <input type="submit" value="Đăng Nhập" />
          <div className="my-1">
            <div className="text-center">
              Bạn chưa có tài khoản ? <Link to="/signup">Đăng ký</Link>
            </div>
          </div>
        </form>
      </section>
    </main>
  );
};

export default styled(Login)`
  width: 100%;
  height: 100vh;
  .form {
    &__img {
      width: 60%;
      display: flex;
      background-color: rgba(0, 0, 0, 0.4);
      flex-direction: column;
      justify-content: space-between;
      padding: 3rem 100px;
      position: relative;
      &::after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        background-image: url(/images/home3-slide-1.jpg);
        background-size: cover;
      }
      p {
        max-width: 400px;
        font-weight: bold;
        font-size: 2.5em;
      }
      &__logo {
        > img {
          width: 200px;
        }
      }
      &__link {
        a {
          margin-right: 10px;
          font-weight: bolder;
        }
      }
      @media ${(props) => props.theme.breakPoints.xl} {
        width: 50%;
      }
    }
    &__form {
      text-align: center;
      width: 40%;
      height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 0 40px;
      > form {
        margin: 0 auto;
        width: 400px;
        display: flex;
        flex-direction: column;
        input {
          text-indent: 20px;
          background-color: black;
          color: #fafafa;
          height: 50px;
          margin: 10px 0;
          border-radius: 8px;
          border: 1px solid #fafafa;
          font-weight: bold;
          outline: none;
          transition: border 0.2s ease-in;
          &:focus {
            border: 3px solid ${(props) => props.theme.colorSecondary};
          }
        }
        input[type="submit"] {
          background-color: ${(props) => props.theme.colorSecondary};
          color: #000;
          border-width: 3px;
          border-color: ${(props) => props.theme.colorSecondary};
          &:hover {
          }
        }
        input[type="date"] {
          text-indent: 10px;
          &::-webkit-calendar-picker-indicator {
            cursor: pointer;
            filter: invert(1);
            margin-right: 10px;
          }
        }
        input::-webkit-input-placeholder {
          color: #fafafa;
        }
      }
      @media ${(props) => props.theme.breakPoints.xl} {
        width: 50%;
      }
      @media ${(props) => props.theme.breakPoints.lg} {
        width: 100%;
      }
      @media ${(props) => props.theme.breakPoints.sm} {
        > form {
          width: 100%;
        }
      }
    }
  }
`;
